.recentGames  {
    width: 100%;
    border-radius: 5px;
    border-collapse: collapse;
}
.recentGames thead th {
    background-color: #001b3b;
    padding: 10px 0;
}
.recentGame {
    background-color: #001f44;
    margin-bottom: 5px;
}
.recentGame .tdContent {
    display: flex;
    justify-content: center;
    align-items: center;
}
.recentGame .user svg {
    width: 50px;
    height: 50px;
    object-fit: cover;
}
.recentGame .coin img {
    width: 20px;
    height: 20px;
    margin-left: 5px;
    object-fit: cover;
}
.recentGame .result img {
    width: 40px;
    height: 40px;
    object-fit: cover;
}