.Chat {
    position: fixed;
    top: 100px;
    right: 20px;
    height: calc(80vh - 80px);
    background: #041b37;
    width: 260px;
    scrollbar-width: 1px;
}
    .Chat .chat {
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 0 5px;
        padding-top: 10px;
        justify-content: space-between;
    }
    .Chat .chatHeader {
        padding: 10px;
        padding-top: 0;
        border-bottom: 1px dotted #092d58;
    }
    .Chat .sendMsgB {
        width: 30px;
        height: 30px;
        margin-left: 10px;
        color: white;
        font-weight: 600;
        background: #092d588a;
        border: none;
    }
    .Chat .sendMsgB svg {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .Chat .chatHeader h2 {
        margin: 0
    }
    .Chat .chat .messages {
        overflow-y: scroll;
    }
    .Chat .chat .inputMessage {
        border-top: 1px dotted #092d58;
        padding: 20px 0;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .Chat .chat .inputMessage .timer {
        margin-left: 10px;
    }
    .chat .inputMessage textarea {
        background: #00152e;
        border: 1px solid #092d58;
        color: white;
        border-radius: 5px;
        padding: 6px;
        resize: none;
        min-height: 30px;
        max-height: 200px;

    }
    .chat .inputMessage textarea:focus {
        outline: 1px solid #7444ce;
    }
    .chat .message {
        display: flex;
    }
    .chat .message .content {
        margin-top: 5px;
        margin-bottom: 20px;
        word-break: break-all;
    }
    .chat .message .content .address {
        font-size: 15px;
        font-weight: 600;
        margin-right: 5px;
        color: white;
        text-decoration: none;
    }
    .chat .message .avatar {
        width: 35px;
        height: 35px;
        object-fit: contain;
    }