
.UserGames {
    background: #041c3a;
    border-radius: 5px;
    border-collapse: collapse;
    width: 100%;
    overflow-x: auto;
    white-space: nowrap;
}

.UserGames tbody tr {
    background-color: #092546;
}

.UserGames tbody tr:nth-child(2n){
    background-color: #031327;
}

.UserGames tbody td {
    padding: 7px 15px;
}
.UserGames .game img {
    width: 30px;
    height: 30px;
    object-fit: cover;
}

.openFairnessB {
    padding: 8px 20px;
    background-color: #746bf5;
    color: #F5F5F5;
    border: none;
    border-radius: 5px;
}
.Profile .info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Profile .gameStats {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 90%;
}

.Profile .gameStat {
    color: rgba(211, 211, 211, 0.849);
}

.UserGames thead {
    background: #031327;
} 

.UserGames .win {
    color: rgb(37, 185, 0);
}

.UserGames .lose {
    color: rgb(185, 37, 0);
}