.Navbar {
    background-color: #012552;
    position: fixed;
    width: 100vw;
    height: 80px;
    padding: 5px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 1;
}
    .Navbar .left {
        display: flex;
        align-items: center;
    }
    .Navbar .left .navigation {
        margin-left: 10px;
    }
    .Navbar .left .navigation .link:hover {
        border-bottom: 2px solid #746bf5;
    }
    .Navbar .logo {
        display: flex;
        align-items: center;
    }
    .Navbar .logo .title {
        margin-left: 10px;
        font-size: 24px;
        font-family: 'Squared';
        font-weight: 200;
        letter-spacing: 3px;
    }
    .Navbar .logo img {
        width: 52px;
        height: 52px;
        object-fit: cover;
    }
    .Navbar .user {
        display: flex;
        align-items: center;

    }
    .Navbar .user .balance {
        height: 30px;
        width: 100px;
        display: flex;
        align-items: center;
        background-color: #00152e;
        border-radius: 5px;
        padding: 0 5px;
        margin-right: 15px;
    }
    .Navbar .user .balance img {
        height: 20px;
        width: 20px;
        object-fit: cover;
    }
    .Navbar .user .balance .amount {
        padding: 5px;
    }
    .Navbar .user .balance .amount[data-balancechanged='1'] {
        animation: balanceIncrease 2.5s 1;
    }
    .Navbar .user .balance .amount[data-balancechanged='-1'] {
        animation: balanceDecrease 2.5s 1;
    }
    .Navbar .user .withdrawB,
    .Navbar .user .depositB {
        padding: 10px 30px;
        background-color: #4e44ce;
        font-weight: 600;
        color: #F5F5F5;
        margin-right: 15px;
        border: none;
        border-radius: 5px;
    }
    .Navbar .user .withdrawB {
        background-color: #746bf5;
    }
    .Navbar .user .profile {
        display: flex;
        color: white;
        text-decoration: none;
        background-color: rgb(0, 45, 99);
        border-radius: 5px;
        align-items: center;
        padding-right: 7px;
    }
    .Navbar .user .profile svg {
        width: 40px;
    }
    .Navbar .user .profile h4 {
        margin: 0;
    }

    .Navbar .pending {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .Navbar .pending .withdrawLabel {
        margin-right: 7px;
    }

@keyframes balanceIncrease {
    from {
        color: white;
    }
    50% {
        color: rgb(37, 185, 0);
    }
    to {
        color: white;
    }
}
@keyframes balanceDecrease {
    from {
        color: white;
    }
    50% {
        color: rgb(185, 37, 0);
    }
    to {
        color: white;
    }
}