.ActiveGames {
    padding: 40px;
    text-align: center;
    width: calc(100% - 270px);
}
    .ActiveGames .typeWrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .ActiveGames .typeHeader {
        text-align: left;
        position: relative;
    }
    .ActiveGames .createGameB {
        padding: 10px 30px;
        color: rgba(211, 211, 211);
        background-color: #7444ce;
        font-size: 18px;
        font-weight: 600;
        color: #F5F5F5;
        border: none;
        border-radius: 5px;
    }
    .ActiveGames .gamesCounter {
        color: #7444ce;
    }
    .ActiveGames .typePlaceHolder {
        border-radius: 5px;
        padding: 10px;
        background-color: #041b37;
    }
    .ActiveGames .games {
        display: grid;
        grid-template-columns: repeat(auto-fit, 400px);
        align-items: center;
        justify-content: space-around;
    }
    .ActiveGames .gameWrapper {
        background-color: #001f44;
        display:inline-block;
        padding: 12px;
        border-radius: 5px;
        margin-bottom: 20px;
        margin-right: 10px;
        position: relative;
    }
    .ActiveGames .gameWrapper .closeB {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 10px;
        right: 10px;
        padding: 10px;
        cursor: pointer;
        background: none;
        border: none;
        border-radius: 6px;
    }
    .ActiveGames .gameWrapper .avatar {
        width: 90px;
        height: 90px;
        object-fit: contain;
    }
    .ActiveGames .gameWrapper .avatarPlaceholder {
        width: 65px;
        height: 65px;
        margin-top: 8px;
        object-fit: contain;
    }
    .ActiveGames .gameWrapper .closeB svg {
        fill: #777;
        transition: fill 200ms ease 0s;
    }
    .ActiveGames .gameWrapper .info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 18px;
    }
    .ActiveGames .info .joinB {
        padding: 10px 30px;
        background-color: #4e44ce;
        font-weight: 600;
        color: #F5F5F5;
        border: none;
        border-radius: 5px;
    }
    .ActiveGames .gameWrapper .bet {
        display: flex;
        align-items: center;
    }
    .ActiveGames .gameWrapper .bet img {
        width: 25px;
        height: 25px;
        margin-left: 5px;
        object-fit: cover;
    }
    .ActiveGames .gameWrapper .bet h3 {
        margin: 0;
        font-size: 28px;
    }
    .ActiveGames .gameWrapper .deleteB {
        width: 38px;
        height: 38px;
        background-color: #001b3b;
        border-radius: 50%;
        border: none;
    }
    .ActiveGames .gameWrapper svg {
        width: 80%;
        height: 80%;
        object-fit: cover;
    }
    .ActiveGames .players {
        display: flex;
        align-items: center;
    }
    .ActiveGames .endedGame {
        -webkit-animation: remove 2s linear forwards; 
        animation: remove 2s linear forwards;
    }
    .ActiveGames .players .player {
        background-color: #001b3b;
        padding: 20px 20px;
        border-radius: 5px;
        height: 168px;
        width: 148px;
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
    }
    .ActiveGames .players .player .choice {
        position: absolute;
        width: 25px;
        height: 25px;
        object-fit: cover;
        top: 10px;
        right: 10px;
    }
    .ActiveGames .players .middle {
        width: 120px;
        height: 90px;
        font-size: 18px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .ActiveGames .players .player .address {
        margin: 0;
        color: white;
        text-decoration: none;
        font-size: 15px;
        font-weight: 400;
    }
    .ActiveGames .modal .coinChooser {
        display: flex;
        width: 100%;
        justify-content: space-around;
        margin-bottom: 15px;
        cursor: pointer;
    }
    .ActiveGames .modal .coinChooser img {
        width: 75px;
        height: 75px;
        object-fit: cover;
    }
    .ActiveGames .modal .coinChooser .chosen {
        border: 5px solid white;
        border-radius: 50%;
    }
    .ActiveGames .players .loser {
        opacity: 0.6;
    }
    .ActiveGames .players .winner {
        border: 2px solid #ffd700cf;
    }
    .ActiveGames .players .crown {
        width: 50px;
        height: 50px;
        object-fit: cover;
        position: absolute;
        left: 10px;
        margin: 0 auto;
        transform: rotate(-45deg);
    }

@keyframes remove {
    from { 
        opacity: 1;
        display: flex;
    }
    99% {
        opacity: 0;
    }
    to { 
        display: none;
     }
}