.Profile {
    padding: 40px;
    text-align: center;
    width: calc(100% - 270px);
}

.Profile .profileBox {
    background: #041b37;
    margin-bottom: 20px;
    padding: 40px;
    display: flex;
    border-radius: 5px;
    width: 100%;
}

.profileBox .avatar {
    width: 200px;
    height: 200px;
    object-fit: cover;
}

.profileBox .info {
    flex: 100;
    text-align: center;
}


.Profile .address {
    display: flex;
}

.Profile .address .scanLink {
    display: block;
    cursor: pointer;
}

.Profile .statCircle {
    width: 75px;
    height: 75px;
    border: double 4px transparent;
    border-radius: 80px;
    background-origin: border-box;
    background-clip: content-box, border-box;
}
.Profile .address .scanLink img {
    width: 30px;
    height: 30px;
    object-fit: cover;
}
