#coinFlip {
    position: relative;
    width: 90px;
    transform-style: preserve-3d;
}
#coinFlip .coin0 img, #coinFlip .coin1 img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#coinFlip .coin0, #coinFlip .coin1 {
    position: absolute;
    width: 90px;
    height: 90px;
}

#coinFlip .coin0 {
    transform: translateZ(1px);
    border-radius: 50%;
}

#coinFlip .coin1 {
    transform: translateZ(-1px) rotateY(180deg);
    border-radius: 50%;
}

#coinFlip.spin900 {
    -webkit-animation: rotate900 3s linear forwards; 
    animation: rotate900 3s linear forwards;
}

#coinFlip.spin1080 {
    -webkit-animation: rotate1080 3s linear forwards; 
    animation: rotate1080 3s linear forwards;
}

#coinFlip.spin1260 {
